import React, { useState, useContext } from "react";
import { Redirect } from "react-router-dom";
import {
	Button,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	DropdownItem,
	Form,
	FormGroup,
	Input,
} from "reactstrap";
import { Link } from "react-router-dom";
import appAuth from "../Authentication/base";
import { AuthContext } from "../Authentication/Auth";
import LoginValidation from "./Validation/LoginValidation";
import ForgotPasswordValidation from "./Validation/EmailValidation";
import {
	ShowSuccessNotification,
	ShowDangerNotification,
} from "./Shared/notification";
import Logo from "../Images/Logo.png";

// import axios from "axios";

export default function Login(props) {
	// set initail states

	let [username, setUsername] = useState("");
	let [password, setPassword] = useState("");
	let [errors, setErrors] = useState({});
	let [modal, setModal] = useState(false);

	let toggle = () => setModal(!modal);

	function handleUsernameChange(e) {
		// on change function for title
		setUsername(e.target.value);
	}

	function handlePasswordChange(e) {
		// on change function for description
		setPassword(e.target.value);
	}

	function validate() {
		let valErrors = LoginValidation({ username, password });
		if (Object.keys(valErrors).length === 0) {
			handleSubmit();
		} else {
			setErrors(valErrors);
		}
	}

	function handleSubmit() {
		// stops browser from refreshing
		// e.preventDefault();

		appAuth
			.auth()
			.signInWithEmailAndPassword(username, password)
			.then(() => {
				// return <Redirect to="/Dash" />;
			})
			.catch((err) => {
				const message = err.message;
				ShowDangerNotification(message);
			});
	}

	function forgotPassword() {
		let valErrors = ForgotPasswordValidation({ username });
		console.log(valErrors);
		if (Object.keys(valErrors).length === 0) {
			appAuth
				.auth()
				.sendPasswordResetEmail(username)
				.then(() => {
					const message = "Please check your email to reset password!";
					ShowSuccessNotification(message);
				})
				.catch((err) => {
					const message = err.message;
					ShowDangerNotification(message);
				});
		} else {
			setErrors(valErrors);
		}
	}

	// function clear() {
	// 	setPassword("");
	// 	setUsername("");
	// }

	const { currentUser } = useContext(AuthContext);

	if (currentUser) {
		return <Redirect to="/Dash" />;
	}

	return (
		<React.Fragment>
			<DropdownItem onClick={toggle}>Sign In</DropdownItem>
			<Modal isOpen={modal} toggle={toggle} centered className="px-5 f-bg">
				<Form className="bg-transparent">
					<ModalBody className="bg-transparent">
						<fieldset className="border-main p-3">
							<legend className="text-center">
								<img src={Logo} alt="SBWL DATA" style={{ width: "20%" }} />
							</legend>
							<FormGroup>
								<Input
									name="username"
									type="text"
									value={username}
									onChange={handleUsernameChange}
									placeholder="Email"
									autofocus
									className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
										errors.username ? "is-invalid" : ""
									}`}
								/>
								<div className="text-danger small mt-1 font-italic">
									{errors.username}
								</div>
							</FormGroup>
							<FormGroup>
								<Input
									name="password"
									type="password"
									value={password}
									onChange={handlePasswordChange}
									className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
										errors.password ? "is-invalid" : ""
									}`}
									placeholder="Password"
								/>
								<div className="text-danger small mt-1 font-italic">
									{errors.password}
								</div>
							</FormGroup>
							<div className="custom-control custom-checkbox">
								<input
									type="checkbox"
									className="custom-control-input"
									id="customCheck1"
								/>
								<label
									className="custom-control-label text-white"
									for="customCheck1"
								>
									Remember me
								</label>
							</div>
							<div className="mt-3">
								<Button
									type="button"
									className="bg-main border-0 text-white"
									onClick={() => {
										validate();
									}}
									outline
									block
								>
									Login
								</Button>
								<Button color="secondary" onClick={toggle} block>
									Cancel
								</Button>
								<div className="text-center w-100">
									<Button
										color="link"
										type="button"
										onClick={forgotPassword}
										className="mt-3 text-main"
									>
										forgot password?
									</Button>
								</div>
							</div>
						</fieldset>
					</ModalBody>
				</Form>
			</Modal>
		</React.Fragment>
	);
}
