import React from "react";
import { Card, CardFooter, CardBody, Button } from "reactstrap";
// import BGI from "../Images/mesh.png";
import Col from "reactstrap/lib/Col";
import Row from "reactstrap/lib/Row";
import Slider from "react-slick";
import productsArray from "./DummyData/products";
import sim from "../Images/sim.png";
import Details from "./Details";

const Offers = (props) => {
	let lgSettings = {
		dots: true,
		infinite: false,
		speed: 500,
		arrows: true,
		slidesToShow: 3,
		slidesToScroll: 3,
	};

	let smSettings = {
		dots: true,
		infinite: false,
		speed: 500,
		arrows: true,
		slidesToShow: 1,
		slidesToScroll: 1,
	};

	return (
		<React.Fragment>
			<div className="py-5" id="deals">
				<p className="display-4 text-center my-5 font-weight-bold text-main">
					Deals
				</p>
				{/* will be hidden on screen less than lg */}
				<div className="text-center text-lg-right mx-5 px-2 my-0">
					<a
						href="https://secure.telkom.co.za/today/ucm/"
						className="btn bg-main text-white border-0"
						target="_blank"
						rel="noopener noreferrer"
					>
						Check Coverage
					</a>
				</div>
				<div className="d-none d-lg-block">
					<Slider {...lgSettings} className="mx-5 text-center py-5">
						{productCards()}
					</Slider>
				</div>
				<div className="d-lg-none">
					{/* will be hidden on screen more than lg */}
					<Slider {...smSettings} className="mx-5 text-center py-5">
						{productCards()}
					</Slider>
				</div>
			</div>
		</React.Fragment>
	);
};

export default Offers;

export const productCards = () => {
	return productsArray.map((item, i) => (
		<div className="out">
			<Card className="mx-2 flip_card border-0">
				<div className="card_front text-white h2 rounded w-100">
					<CardBody className="rounded-0 text-center">
						<fieldset className="border border-white p-3">
							<legend className="h2">
								<Row>
									<Col>
										<p className="font-weight-bold">
											{item.name} <br />
											<small className="text-muted h5">anytime</small>
										</p>
									</Col>
									<div className="d-none d-md-block">+</div>
									<Col>
										<p className="font-weight-bold">
											{item.name} <br />
											<small className="text-muted h5">night data</small>
										</p>
									</Col>
								</Row>
							</legend>
							<div className="d-flex justify-content-center">
								<img src={sim} style={{ width: "25%" }} alt="Data Package" />
							</div>
							<div>
								<Col className="h2 text-white font-weight-bold">
									{item.price}
								</Col>
								<Details />
							</div>
						</fieldset>
					</CardBody>
				</div>
				<div className="card_back text-white rounded h-100">
					<CardBody className="d-flex align-items-center justify-content-center h-100">
						<fieldset className="text-left border border-white p-3">
							<legend className="text-white text-center">
								<Row>
									<Col>
										<p className="font-weight-bold h2">
											{item.name} <br />
											<small className="text-muted h5">anytime</small>
										</p>
									</Col>
									<div className="d-none d-md-block">+</div>
									<Col>
										<p className="font-weight-bold h2">
											{item.name} <br />
											<small className="text-muted h5">night data</small>
										</p>
									</Col>
								</Row>
							</legend>
							<ul>
								<li>Month-to-Month</li>
								<li>1 Month data rollover</li>
								<li>No out-of-bundle</li>
								<li>
									LTE ready, just insert sim into deivce and you are ready
								</li>
								<li>3 in 1 breakable sim fits into any device</li>
							</ul>
						</fieldset>
					</CardBody>
				</div>
			</Card>
		</div>
	));
};
