import React, { useState, useEffect, useContext } from "react";
import { Button, Input, FormGroup, Form } from "reactstrap";
import PersonalModal from "../Components/DashComponents/PersonalModal";
import ServiceModal from "../Components/DashComponents/ServiceModal";
import appAuth from "../Authentication/base";
import { AuthContext } from "../Authentication/Auth";

function CustomerDash() {
	const { currentUser } = useContext(AuthContext);
	const [personalInfo, setPersonalInfo] = useState({});
	// const [deliveryInfo, setDeliveryInfo] = useState({});
	const [productInfo, setProductInfo] = useState({});

	function handleLogout() {
		appAuth.auth().signOut();
	}

	useEffect(() => {
		appAuth
			.firestore()
			.collection("personal")
			.doc(currentUser.uid)
			.get()
			.then((doc) => {
				setPersonalInfo(doc.data());
			});
		appAuth
			.firestore()
			.collection("product")
			.doc(currentUser.uid)
			.get()
			.then((doc) => {
				setProductInfo(doc.data());
			});
	}, [currentUser.uid]);

	function handlePersonalChange(e) {
		let updatePersonalInfo = personalInfo;
		updatePersonalInfo[e.target.name] = e.target.value;
		setPersonalInfo(updatePersonalInfo);
	}

	return (
		<React.Fragment>
			<div className="h-100 p-5">
				<p className="h1 text-main">
					Welcome {personalInfo.name}
					<small className="h5 float-right">
						<Button onClick={handleLogout}>Logout</Button>
					</small>
				</p>
				<hr />
				<div className="row">
					<fieldset className="col-12 col-lg-4 mt-5 border-main">
						<legend className="h3 text-main">Personal details</legend>
						<p className="text-muted">
							{personalInfo.name} {personalInfo.surname}
						</p>
						<p className="text-muted">{personalInfo.id}</p>
						<p className="text-muted">{personalInfo.cellphone}</p>
						<p className="text-muted">{personalInfo.email}</p>
						<p className="text-muted">
							{personalInfo.street}, {personalInfo.suburb}, {personalInfo.town},{" "}
							{personalInfo.postCode}
						</p>
						<PersonalModal
							personalInfo={personalInfo}
							handlePersonalChange={handlePersonalChange}
							currentUser={currentUser}
						/>
					</fieldset>
					<fieldset className="col-12 col-lg-4 mt-5 border-main">
						<legend className="h3 text-main">Services</legend>
						<p className="text-muted">
							{productInfo.name} + {productInfo.name}{" "}
							<small>month-to-month</small>
						</p>
						<p className="text-muted">{productInfo.price}</p>
						<ServiceModal />
					</fieldset>
					<fieldset className="col-12 col-lg-4 mt-5 border-main">
						<legend className="h3 text-main">Manage account</legend>
						<Form autoComplete="off">
							<FormGroup className="mt-3">
								<Input type="password" placeholder="current password" />
							</FormGroup>
							<FormGroup className="mt-3">
								<Input type="password" placeholder="new password" />
							</FormGroup>
							<FormGroup className="mt-3">
								<Input type="password" placeholder="confirm password" />
							</FormGroup>
							<Button className="bg-main border-0">change</Button>
						</Form>
					</fieldset>
				</div>
			</div>
		</React.Fragment>
	);
}

export default CustomerDash;
