import React, { useState } from "react";
import { FormGroup, Button } from "reactstrap";
import ProductValidaion from "../Validation/ProductValidaion";

function Product({ product, products, handleProductChange, next, back }) {
	const [errors, setErrors] = useState({});

	return (
		<React.Fragment>
			<fieldset className="col-12 col-lg-4 offset-lg-4 border-main px-5 pb-5">
				<legend className="h3 text-main text-center">Data Package</legend>
				<FormGroup className="form-row">
					<select
						className={`custom-select mt-3 input ${
							errors.product ? "is-invalid" : ""
						}`}
						// defaultValue={product}
						onChange={handleProductChange}
					>
						{/* check if product is and display details or ask to select a product */}
						{product.name !== null ? (
							<option>{`${product.name} + ${product.name} - ${product.price}`}</option>
						) : (
							<option value={null}>Please select a product</option>
						)}
						{/* map through all roducts */}
						{products.map((item, i) => (
							<option key={i} value={i}>
								{item.name} + {item.name} - {item.price}
							</option>
						))}
					</select>
					<div className="text-danger small mt-1 font-italic">
						{errors.product}
					</div>
				</FormGroup>
				<Button
					className="bg-main border-0 text-white"
					block
					onClick={() => {
						setErrors(next(product, ProductValidaion));
					}}
				>
					Next
				</Button>
				<Button
					color="secondary"
					block
					onClick={() => {
						back();
					}}
				>
					Back
				</Button>
			</fieldset>
		</React.Fragment>
	);
}

export default Product;
