import React, { useState } from "react";
import { Button } from "reactstrap";
import {
	ShowSuccessNotification,
	ShowDangerNotification,
} from "../Shared/notification";
import * as emailjs from "emailjs-com";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { Redirect } from "react-router-dom";
import appAuth from "../../Authentication/base";

function Summary({ back, personalInfo, deliveryInfo, product }) {
	const [loading, setLoading] = useState(false);
	const [redirect, setRedirect] = useState(false);

	const handleSubmit = () => {
		setLoading(true);
		// e.preventDefault();

		appAuth
			.auth()
			.createUserWithEmailAndPassword(personalInfo.email, personalInfo.password)
			.then((user) => {
				console.log(user);
				// Add personal data to the firestore database
				delete personalInfo.password;
				delete personalInfo.confirmPassword;
				appAuth
					.firestore()
					.collection("personal")
					.doc(user.user.uid)
					.set({ ...personalInfo, ...deliveryInfo });

				appAuth
					.firestore()
					.collection("product")
					.doc(user.user.uid)
					.set(product);

				emailjs
					.sendForm(
						"smtp_server",
						"order_template",
						"order_form",
						"user_2NUlgzcOgBlL7JKjxQraj"
					)
					.then(() => {
						const message =
							"Your order has been placed. Please see your email for more information!";
						ShowSuccessNotification(message);
						setLoading(false);
						setRedirect(true);
					})
					.catch(() => {
						const message =
							"Oops. Something went wrong with emailjs. Please ensure you are connected to the internet or contact administrator at admin@sbwldat.co.za";
						ShowDangerNotification(message);
						setLoading(false);
					});
			})
			.catch((err) => {
				const message = err.message;
				ShowDangerNotification(message);
				setLoading(false);
			});
	};

	if (redirect) {
		return <Redirect to="/" />;
	}
	return (
		<React.Fragment>
			<fieldset className="col-12 col-lg-6 offset-lg-3 px-5 pb-5 border-main">
				<legend className=" h3 text-main text-center">
					Summary Information
				</legend>
				<table className="mb-5">
					<tr>
						<td>
							<b>Name: </b>
						</td>
						<td className="text-muted">{personalInfo.name}</td>
					</tr>
					<tr>
						<td>
							<b>Surname: </b>
						</td>
						<td className="text-muted">{personalInfo.surname}</td>
					</tr>
					<tr>
						<td>
							<b>ID Number: </b>
						</td>
						<td className="text-muted">{personalInfo.id}</td>
					</tr>

					<tr>
						<td>
							<b>Email: </b>
						</td>
						<td className="text-muted">{personalInfo.email}</td>
					</tr>
					<tr>
						<td>
							<b>Mobile Number: </b>
						</td>
						<td className="text-muted">{personalInfo.cellphone}</td>
					</tr>
					<tr>
						<td>
							<b>Data Package: </b>
						</td>
						<td className="text-muted">
							{product.name} <small>anytime</small> + {product.name}{" "}
							<small>anytime</small> - {product.price}
						</td>
					</tr>
					<tr>
						<td>
							<b>Delivery Address: </b>
						</td>
						<td className="text-muted">
							{deliveryInfo.street}, {deliveryInfo.suburb}, {deliveryInfo.town},{" "}
							{deliveryInfo.postCode}
						</td>
					</tr>
				</table>
				<Button
					className="bg-main border-0 text-white"
					block
					disabled={loading}
					onClick={() => {
						handleSubmit();
					}}
				>
					{loading ? <FontAwesomeIcon icon={faSpinner} spin /> : "Place Order"}
				</Button>
				<Button
					color="secondary"
					block
					onClick={() => {
						back();
					}}
				>
					Back
				</Button>
			</fieldset>
			<form id="order_form">
				<input type="text" name="email" value={personalInfo.email} hidden />
				<input type="text" name="name" value={personalInfo.name} hidden />
				<input type="text" name="surname" value={personalInfo.Surname} hidden />
				<input type="text" name="package" value={product.name} hidden />
			</form>
		</React.Fragment>
	);
}

export default Summary;
