import React, { useState } from "react";
// import useForm from "../Shared/useForm";
import validation from "../Components/Validation/ContactUsValidation";
import { Form, FormGroup, Input, Button } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faEnvelope,
	faMobile,
	faSpinner,
} from "@fortawesome/free-solid-svg-icons";
import * as emailjs from "emailjs-com";
import {
	ShowSuccessNotification,
	ShowDangerNotification,
} from "./Shared/notification";

const ContactUs = () => {
	const data = {
		name: null,
		email: null,
		message: null,
	};

	const [contactData, setContactData] = useState(data);
	const [loading, setLoading] = useState(false);
	const [errors, setErrors] = useState({});

	const Validation = (e) => {
		e.preventDefault();
		let validationErrors = validation(contactData);

		if (Object.keys(validationErrors).length === 0) {
			handleSubmit();
		} else {
			setErrors(validationErrors);
		}
	};

	const handleChange = (e) => {
		let updatedContactData = contactData;
		updatedContactData[e.target.name] = e.target.value;
		setContactData(updatedContactData);
	};

	function handleSubmit() {
		emailjs
			.sendForm(
				"smtp_server",
				"contact_us_template",
				"contact_us_form",
				"user_2NUlgzcOgBlL7JKjxQraj"
			)
			.then(() => {
				const message = "You are query has been sent!";
				ShowSuccessNotification(message);
				setLoading(false);
				ClearFormData();
			})
			.catch(() => {
				const message =
					"Oops. Something went wrong. Please ensure you are connected to the internet or contact administrator at admin@sbwldat.co.za";
				ShowDangerNotification(message);
				setLoading(false);
			});
	}
	const ClearFormData = () => {
		setContactData(data);
	};

	return (
		<React.Fragment>
			<div id="contactUs">
				<div className="pt-5 h-100">
					<p className="display-4 text-center my-5 mx-5 font-weight-bold text-white">
						GET IN TOUCH
					</p>
					<div className="row mx-1 justify-content-center text-white">
						<div className="col-12 col-lg-5 d-flex align-items-center justify-content-center">
							<div className="mt-2">
								<div>
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faMobile} />
										076 661 3279
									</p>
								</div>
								<div className="mb-5">
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faEnvelope} />
										info@sbwldata.co.za
									</p>
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faEnvelope} />
										admin@sbwldata.co.za
									</p>
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faEnvelope} />
										support@sbwldata.co.za
									</p>
									<p className="font-italic">
										<FontAwesomeIcon className="mr-2" icon={faEnvelope} />
										accounts@sbwldata.co.za
									</p>
								</div>
							</div>
						</div>
						<div className="col-12 col-lg-5 d-flex align-items-center mb-5">
							<fieldset className="col-12 border border-white">
								<legend className="text-white text-center">
									Want to know More?
								</legend>
								<Form
									className="w-100 b-shadow p-5"
									onSubmit={Validation}
									noValidate
									id="contact_us_form"
								>
									<FormGroup className="text-left my-4">
										<Input
											name="name"
											type="text"
											defaultValue={contactData.name}
											onChange={handleChange}
											autoFocus
											placeholder="Full name"
											className="border-bottom "
										/>
										<div className="text-white small mt-1 font-italic">
											{errors.name}
										</div>
									</FormGroup>
									<FormGroup className="text-left my-4">
										<Input
											name="email"
											type="email"
											defaultValue={contactData.email}
											onChange={handleChange}
											placeholder="Someone@domain.com"
											className="border-bottom"
										/>
										<div className="text-white small mt-1 font-italic">
											{errors.email}
										</div>
									</FormGroup>
									<FormGroup className="text-left my-4">
										{/* <Label className="font-weight-bold">Message</Label> */}
										<textarea
											name="message"
											placeholder="message"
											defaultValue={contactData.message}
											onChange={handleChange}
											className="form-control border-bottom"
											rows="3"
										></textarea>
										<div className="text-white small mt-1 font-italic">
											{errors.message}
										</div>
									</FormGroup>
									<Button
										type="submit"
										block
										color="dark"
										className="mt-4 text-main"
									>
										{loading ? (
											<FontAwesomeIcon icon={faSpinner} spin />
										) : (
											"Send"
										)}
									</Button>
								</Form>
							</fieldset>
						</div>
					</div>
				</div>
			</div>
		</React.Fragment>
	);
};

export default ContactUs;
