import React, { useState } from "react";
import {
	Button,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	Form,
	FormGroup,
	Input,
} from "reactstrap";
import Validation from "../Validation/UpdatePersonalValidation";
import appAuth from "../../Authentication/base";
import {
	ShowSuccessNotification,
	ShowDangerNotification,
} from "../Shared/notification";

export default function PersonalModal({
	personalInfo,
	handlePersonalChange,
	deliveryInfo,
	currentUser,
}) {
	// set initail states

	const [errors, setErrors] = useState({});

	let [modal, setModal] = useState(false);

	let toggle = () => setModal(!modal);

	function Validate() {
		let valErrors = Validation(personalInfo);

		if (Object.keys(valErrors).length === 0) {
			handleSubmit();
		} else {
			setErrors(valErrors);
		}
	}

	function handleSubmit() {
		const db = appAuth.firestore();

		db.collection("personal")
			.doc(currentUser.uid)
			.update(personalInfo)
			.then(() => {
				const message = "Your information has been updated!";
				ShowSuccessNotification(message);
				toggle();
			})
			.catch((err) => {
				const message = err.message;
				ShowDangerNotification(message);
			});
	}

	return (
		<React.Fragment>
			<Button onClick={toggle} className="bg-main border-0">
				edit
			</Button>
			<Modal isOpen={modal} toggle={toggle}>
				<Form className="text-center">
					<ModalHeader toggle={toggle} className="border-0 text-center">
						<p className="text-main">Personal Details</p>
					</ModalHeader>
					<ModalBody>
						<FormGroup>
							<Input
								name="name"
								type="text"
								placeholder="Name"
								defaultValue={personalInfo.name}
								onChange={handlePersonalChange}
								autofocus
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.name}
							</div>
						</FormGroup>
						<FormGroup>
							<Input
								name="surname"
								type="text"
								defaultValue={personalInfo.surname}
								onChange={handlePersonalChange}
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="Surname"
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.surname}
							</div>
						</FormGroup>
						<FormGroup>
							<Input
								name="id"
								type="text"
								defaultValue={personalInfo.id}
								onChange={handlePersonalChange}
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="ID number"
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.id}
							</div>
						</FormGroup>
						<FormGroup>
							<Input
								name="email"
								type="email"
								defaultValue={personalInfo.email}
								onChange={handlePersonalChange}
								disabled
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="Email address"
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.email}
							</div>
						</FormGroup>
						<FormGroup>
							<Input
								name="cellphone"
								type="text"
								defaultValue={personalInfo.cellphone}
								onChange={handlePersonalChange}
								className="border-top-0 border-left-0 border-right-0 rounded-0 input"
								placeholder="Mobile number"
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.cellphone}
							</div>
						</FormGroup>
						<FormGroup className="mt-3">
							<Input
								name="street"
								type="text"
								defaultValue={personalInfo.street}
								onChange={handlePersonalChange}
								placeholder="Street number"
								className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
									errors.street ? "is-invalid" : ""
								}`}
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.street}
							</div>
						</FormGroup>
						<FormGroup className="form-row">
							<Input
								name="suburb"
								type="text"
								defaultValue={personalInfo.suburb}
								onChange={handlePersonalChange}
								placeholder="Suburb"
								className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
									errors.suburb ? "is-invalid" : ""
								}`}
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.suburb}
							</div>
							<Input
								name="town"
								type="text"
								defaultValue={personalInfo.town}
								onChange={handlePersonalChange}
								placeholder="Town"
								className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
									errors.town ? "is-invalid" : ""
								}`}
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.town}
							</div>
						</FormGroup>
						<FormGroup>
							<Input
								name="postCode"
								type="number"
								defaultValue={personalInfo.postCode}
								onChange={handlePersonalChange}
								placeholder="Postal Code / ZIP Code"
								className={`border-top-0 border-left-0 border-right-0 rounded-0 input ${
									errors.postCode ? "is-invalid" : ""
								}`}
							/>
							<div className="text-danger small mt-1 font-italic">
								{errors.postCode}
							</div>
						</FormGroup>
					</ModalBody>
					<ModalFooter className="border-0">
						<Button
							type="button"
							onClick={Validate}
							className="bg-main border-0"
							block
						>
							Save
						</Button>
						<Button color="secondary" onClick={toggle} block>
							Cancel
						</Button>
					</ModalFooter>
				</Form>
			</Modal>
		</React.Fragment>
	);
}
