import React, { useState } from "react";
import Navigation from "../Components/Navigation";
import { Form } from "reactstrap";
// import { useState } from "react";
import PersonalInfo from "../Components/SignUpComponents/PersonalInfo";
import DeliveryInfo from "../Components/SignUpComponents/DeliveryInfo";
import Product from "../Components/SignUpComponents/Product";
import Summary from "../Components/SignUpComponents/Summary";
import Products from "../Components/DummyData/products";

function Signup() {
	const initialProductInfo = {
		name: null,
		price: null,
	};
	const intialPersonalInfo = {
		name: "",
		surname: "",
		id: "",
		email: "",
		cellphone: "",
		password: "",
		confirmPassword: "",
	};

	const intialDeliveryInfo = {
		street: "",
		suburb: "",
		town: "",
		postCode: 6210,
	};
	const [step, setStep] = useState(1);
	let [personalInfo, setPersonalInfo] = useState(intialPersonalInfo);
	let [deliveryInfo, setDeliveryInfo] = useState(intialDeliveryInfo);
	let [product, setProduct] = useState(initialProductInfo);

	function handlePersonalChange(e) {
		let updatePersonalInfo = personalInfo;
		updatePersonalInfo[e.target.name] = e.target.value;
		setPersonalInfo(updatePersonalInfo);
	}

	function handleDeliveryChange(e) {
		let updateDeliveryInfo = deliveryInfo;
		updateDeliveryInfo[e.target.name] = e.target.value;
		setDeliveryInfo(updateDeliveryInfo);
	}
	function handleProductChange(e) {
		setProduct(Products[e.target.value]);
		// let updateProductInfo = deliveryInfo;
		// updateDeliveryInfo[e.target.name] = e.target.value;
		// setProduct(e.target.value);
	}

	function next(values, validate) {
		let errors = validate(values);

		if (Object.keys(errors).length === 0) {
			setStep(step + 1);
			return {};
		} else {
			return errors;
		}
	}

	function back() {
		setStep(step - 1);
	}

	function steps() {
		if (step === 1) {
			return (
				<PersonalInfo
					personalInfo={personalInfo}
					handlePersonalChange={handlePersonalChange}
					next={next}
				/>
			);
		} else if (step === 2) {
			return (
				<Product
					product={product}
					products={Products}
					handleProductChange={handleProductChange}
					next={next}
					back={back}
				/>
			);
		} else if (step === 3) {
			return (
				<DeliveryInfo
					deliveryInfo={deliveryInfo}
					handleDeliveryChange={handleDeliveryChange}
					next={next}
					back={back}
				/>
			);
		} else if (step === 4) {
			return (
				<Summary
					personalInfo={personalInfo}
					deliveryInfo={deliveryInfo}
					product={product}
					back={back}
				/>
			);
		}
	}
	return (
		<React.Fragment>
			<Navigation isHomeNav={false} />
			<Form className="p-5">
				<p className="h2 text-center text-main"> Step {step} of 4</p>
				<div className="row my-5">{steps()}</div>
			</Form>
		</React.Fragment>
	);
}

export default Signup;
