export default function PersonalInfoValidation(values) {
	let errors = {};

	if (!values.name) {
		errors.name = "Name is required!";
	}
	if (!values.surname) {
		errors.surname = "Surname is required!";
	}
	if (!values.email) {
		errors.email = "Email address is required!";
	} else if (!/\S+@\S+\.\S+/.test(values.email)) {
		errors.email = "Email is invalid!";
	}
	if (!values.id) {
		errors.id = "ID number is required!";
	} else if (
		!/(((\d{2}((0[13578]|1[02])(0[1-9]|[12]\d|3[01])|(0[13456789]|1[012])(0[1-9]|[12]\d|30)|02(0[1-9]|1\d|2[0-8])))|([02468][048]|[13579][26])0229))(( |-)(\d{4})( |-)(\d{3})|(\d{7}))/.test(
			values.id
		)
	) {
		errors.id = "ID number is not valid!";
	}
	if (!values.cellphone) {
		errors.cellphone = "cellphone number is required!";
	} else if (
		!/^0[87][23467]((\d{7})|( |-)((\d{3}))( |-)(\d{4})|( |-)(\d{7}))/.test(
			values.cellphone
		)
	) {
		errors.cellphone = "Please provide valid mobile number!";
	}
	if (!values.password) {
		errors.password = "Password is required!";
	} else if (
		!/((?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[\W_]).{6,50})/.test(values.password)
	) {
		errors.password =
			"Password must be at least 6 characters long, 1 upper case character, 1 number, 1 special character!";
	}
	if (!values.confirmPassword) {
		errors.confirmPassword = "Password is required!";
	} else if (values.password !== values.confirmPassword) {
		errors.confirmPassword = "The passwords you have entered do not match!";
	}

	return errors;
}
